@import '../../common/styles/sass/mixins.scss';
@import '../../common/styles/sass/variables.scss';
.skill {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  .icon {
    height: 90px;
    width: 90px;
    background: rgba(255, 255, 255, 0.1);
    display: block;
    line-height: 90px;
    box-shadow: -6px 10px 40px rgb(0 0 0 / 6%);
    text-align: center;
    transform: rotate(45deg);
    transform-origin: center;
    .svg {
      transform: rotate(-45deg);
      margin-top: 14px;
      height: 60px;
      width: 60px;
    }
  }
  .title {
    @include text($titleFontFamily, 16px, 1.2);
    font-weight: 700;
    padding-top: 40px;
    margin: 0;
  }
  // .description {
  //   @include text($textFontFamily, 14px, 1.8);
  //   font-weight: 400;
  //   padding: 10px;
  // }
}
